.sidebar {
    position: fixed;
    width: 100vh;
    height: 100vh;
    top: 0;
    left: 0;
    transform: translate(-100%);
    transition: 0.5s;
}


.sidebar.open {
    transform: translate(0);
    z-index: 2;
    width: 30%;
    height: 100%;
    background-color: rgb(255, 255, 255);
    transition: 0.5s;
    border-radius: 0 12px 12px 0;
}

.background {
    position: absolute;
    width: 100%;
    height: 100vh;
    top: 0;
    background-color: rgba(0, 0, 0, 0.56);
    z-index: 2;

}


.nav {
    padding: 63px 0 0 70px;
    margin-bottom: 20px;
}

.close {
    cursor: pointer;
    background-color: transparent;
    border: none;
    display: flex;
    width: 100%;
    justify-content: flex-end;
    padding: 50px 50px 0 0;
}

.closeNotOpen {
    display: none;
}


.close img {
    width: 40px;
    height: 40px;
}

@media screen and (max-width: 760px) {
    .sidebar.open {
        width: 100%;
        border-radius: 0;
        transition: 0.5s;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .nav {
        padding: 0;
    }

    .close {
        padding: 30px 30px 0 0;
    }
}
